<template>
	<div class="home-container">
		<div class="home-header">
			<div class="home-guntu">
				<!--<img src="../assets/banner2.png">-->
				<mu-carousel>
					<mu-carousel-item v-for="(item,index) in bannerlist" :key="index">
						<img :src="baseUrl+item.banner_pic">
					</mu-carousel-item>
				</mu-carousel>
			</div>
		</div>
		<div class="home-content">
			<!--<div class="home-around">
				<div class="home-tu">
					<img src="../assets/btn_lazn.png" @click="go_guide()">
				</div>
				<div class="home-tu">
					<img src="../assets/btn_qscs.png" @click="go_eqtest()">
				</div>
			</div>
			<div class="home-around">
				<div class="home-tu">
					<img src="../assets/btn_zsm.png" @click="go_show()">
				</div>
				<div class="home-tu" @click="userInfo()">
					<img src="../assets/btn_ql.png">
				</div>
			</div>-->
			<div class="home-around">
				<div  @click="go_guide()">
					<div class="home-tut">
						<img src="../assets/1.png">
					</div>
					<div class="home-typeface"></div>
				</div>
				<div @click="go_eqtest()">
					<div class="home-tut">
						<img src="../assets/2.png">
					</div>
					<div class="home-typeface"></div>
				</div>
				<div @click="go_show()">
					<div class="home-tut">
						<img src="../assets/3.png">
					</div>
					<div class="home-typeface"></div>
				</div>
				<div @click="userInfo()">
					<div class="home-tut">
						<img src="../assets/4.png">
					</div>
					<div class="home-typeface"></div>
				</div>
			</div>
			<div class="home-line"></div>
			<div class="home-chuqi"></div>
			<div class="home-flex" v-for="item in special_list" @click="go_article(item.article_id)">
				<div class="home-tup"><!--:style="'backgroundImage:url('+item.article_pic+')'"-->
					<!--<img src="../assets/img_qscs.png">-->
					<img :src="!!item.article_pic?baseUrl+item.article_pic:'../assets/img_qscs.png'">
				</div>
				<div>
					<div class="home-hua">{{item.article_title}}</div>
					<!--<div class="home-shi">{{$root.formatDate(item.create_time)}}</div>-->
					<div class="home-shi">{{item.create_time}}</div>
				</div>
			</div>
			<!--<div class="home-flex">
				<div class="home-tup">
					<img src="../assets/img_qscs.png">
				</div>
				<div>
					<div class="home-hua"></div>
					<div class="home-shi">2</div>
				</div>
			</div>-->
		</div>
		<div class="home-footer">
			<mu-bottom-nav>
				<mu-bottom-nav-item title="" icon=" " to="home" tag="span"></mu-bottom-nav-item>
				<mu-bottom-nav-item title="" icon=" " to="verbal" tag="span"></mu-bottom-nav-item>
				<mu-bottom-nav-item title="" icon=" " to="discussion" tag="span"></mu-bottom-nav-item>
				<mu-bottom-nav-item title="" icon=" " to="mine" tag="span"></mu-bottom-nav-item>
			</mu-bottom-nav>
		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				special_list: [],
				data: [],
				bannerlist: '',
				data: {}
			}
		},
		methods: {
			go_guide() {
				this.$router.push('/guide')
			},
			go_eqtest() {
				this.$router.push('/eqtest')
			},
			go_show() {
				this.$router.push('/show')
			},
			go_groupchat() {
				this.$router.push('/groupchat')
			},
			go_article(article_id){
				this.$router.push({
					path:'/article',
					query:{
						actile:article_id
					}
				})
			},
//			
			articles() {
				var that = this;
				that.$http.post('/article/articles', {}).then((res) => {
					if(res.meta.code == 200) {
						that.special_list = res.data;
						that.special_list = that.special_list.reverse();
					}
				})
			},
//			
			banner() {
				var that = this;
				that.$http.post('/banner/banners', {}).then((res) => {
					if(res.meta.code == 200) {
					that.bannerlist = res.data;
					}
				})
			},
			//			
			userInfo() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
//					discussionid: that.discussionid
				};
				that.$http.post('/user/userInfo', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.data = res.data;
						if(that.data.user_vip == '3'){
//							this.$router.push('/groupchat')
							that.go_groupchat()
						}else{
							that.$alert('','');
						}
					}
				})
			}
		},
		created() {
			// 
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
			this.banner();
			this.articles();
		},
		mounted() {
			//  dom  

		}
	}
</script>

<style scoped>
	.home-container {
		width: 100%;
		height: 100%;
		background: #FFFFFF;
	}
	
	.home-header {
		width: 95%;
		margin: 0 auto;
		height: 160px;
		margin-bottom: 1.5rem;
		/*padding-top: 1rem;*/
	}
	
	.home-guntu img {
		width: 100%;
		height: 150px;
		border-radius: 10px;
	}
	
	.home-guntu .mu-carousel {
		height: 180px;
	}
	
	.home-content {
		width: 100%;
		height: calc(100% - 255px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.home-around {
		display: flex;
		justify-content: space-around;
	}
	.home-tut img{
		width: auto;
		height: 4.5rem;
	}
	.home-typeface{
		font-size: 1.5rem;
		padding-bottom: 1rem;
		text-align: center;
	}
	.home-tu img {
		width: auto;
		height: 6rem;
		margin-bottom: 1.5rem;
	}
	
	.home-line {
		border: 5px solid #EAEAEA;
		margin-bottom: 1rem;
	}
	
	.home-chuqi {
		font-size: 1.5rem;
		color: #000000;
		border-left: 5px solid #fd5997;
		padding-left: 0.8rem;
		margin-left: 1.5rem;
		font-weight: 550;
	}
	
	.home-flex {
		display: flex;
	}
	
	.home-tup img {
		width: 7rem;
		height: 5rem;
		margin: 1.5rem 1.5rem 0.5rem 1.5rem;
	}
	
	.home-hua {
		width: 100%;
		font-size: 1.4rem;
		color: #000000;
		padding-right: 1.5rem;
		padding-top: 1.2rem;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	
	.home-shi {
		font-size: 1.2rem;
		color: #8A8A8A;
		padding-top: 0.5rem;
		position: relative;
		margin-bottom: -1.5rem;
	}
	
	.home-footer {
		height: 55px;
		box-sizing: border-box;
		overflow: hidden;
		position: fixed;
		bottom: 0;
		width: 100%;
		background: #fff;
		border-top: 1px solid #f0f0f0;
	}
	
	/deep/ .mu-tabs-inverse {
		background-color: #FFFFFF;
	}
	
	/deep/ .mu-tab-wrapper {
		padding: 0;
	}
	
	/deep/ .mu-bottom-item-icon {
		margin-top: 6px;
		background-repeat: no-repeat;
		width: 20px;
		height: 20px;
		background-size: 100% 100%;
	}
	/*/deep/ .mu-bottom-item-text {
		color: #999999
	}*/
	/*/deep/ .mu-bottom-item-active .mu-bottom-item-text {
		color: #229efe;
	}*/
	
	/deep/ .mu-bottom-item-active {
		color: rgba(0, 0, 0, .54);
	}
	
	/deep/ .mu-bottom-item:nth-child(1) .mu-bottom-item-icon {
		background-image: url(../assets/shouye.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(2) .mu-bottom-item-icon {
		background-image: url(../assets/tab_huashu1.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(3) .mu-bottom-item-icon {
		background-image: url(../assets/tab_taolun1.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(4) .mu-bottom-item-icon {
		background-image: url(../assets/tab_wode1.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(1) .mu-bottom-item-text {
		color: #f683a2;
		font-size: 14px;
	}
	
	/deep/ .mu-bottom-item {
		min-width: 0px;
	}
	/deep/ .mu-carousel-button.mu-icon-button {
		z-index: 0;
	}
	/deep/ .mu-carousel-indicators{
		z-index: 0;
	}
</style>


